nav {
  mix-blend-mode: difference;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 50px;
  width: 100vw;
  height: 54px;
  top: 0;
  left: 0;
  box-sizing: border-box;
  position: fixed;
  font-weight: bold;
  z-index: 99;
}

.name,
.portfolio {
  color: rgb(212, 212, 212);
}

.toggleContainer {
  width: 50px;
  height: 20px;
  border-radius: 30px;
  cursor: pointer;
  background-color: rgb(212, 212, 212);
}

.icon {
  color: black !important;
  font-size: 2rem;
  transition: transform 0.5s;
}
